* {
    font-family: Montserrat, serif;
    margin: 0;
    padding: 0;
}

.workerInList {
    font-weight: 400;
    font-size: 22px;
    padding: 10px 20px;
    cursor: pointer;
}

.listOfWorkers {
    background-color: azure;
    padding-top: 30px;
    overflow-y: auto;
    overflow-x: hidden;
    margin-bottom: 500px;
    border-radius: 10px;
}

.mainClass {
    display: flex;
    height: 100vh;
}

.inputFields {
    display: flex;
    height: max-content;
}

.info {
    padding: 30px;
}

.info > button {
    background-color: unset;
    border: none;
    padding-top: 20px;
    text-transform: uppercase;
    cursor: pointer;
    font-size: 20px;
    font-weight: 700;
    color: deepskyblue
}

.inputField {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.inputField > h2, .inputField > input  {
    font-size: 20px;
    font-weight: 400;
    margin-right: 20px;
}

.inputField > input {
    width: 350px;
}

.inputField > input:focus {
    outline: none;
}